<template>
  <div class="rating_page">
      <header-top head-title="商家详情" go-back="true"></header-top>
     <section class="shopDetail_container">
         <header>
             活动于属性
         </header>
        <ul class="actibities_ul">
            <li v-for="item in shopDetail.supports" :key="item.id">
                <span class="supports_title" :style='{backgroundColor:"#"+item.icon_color}'>{{item.icon_name}}</span>
                <span class="supports_text">{{item.description}}(App专享)</span>
            </li>
        </ul>
     </section>
     <section class="shiping_jianca">
         <router-link class="indesc_container" to="/shop/shopDetail/shopSafe">
             <span class="indesc_title">食品监督安全公示</span>
             <div class="indesc_text">
                <span>企业认证详情</span>
                <svg width="14" height="14" xmlns="http://www.w3.org/2000/svg" version="1.1" class="description_arrow" >
                        <path d="M0 0 L8 7 L0 14"  stroke="#bbb" stroke-width="1.5" fill="none"/>
                </svg>
             </div>
         </router-link>
         <section class="img_rout">
             <div>
                <svg class="shop_status" v-if="shopDetail.status == 1">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#res-well"></use>
                </svg>
                    <svg class="res-well" v-else>
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#res-bad"></use>
                    </svg>
             </div>
             <div class="jianca_tx">
                 <p>
                    <span>监督检查结果:</span>
                    <span v-if="shopDetail.status==1" class="Success">良好</span>
                    <span v-else class="Error">差</span>
                </p>
                <p>
                    <span>检查日期:</span>
                    <span>{{shopDetail.identification.identificate_date && shopDetail.identification.identificate_date.split('T')[0]}}</span>
                </p>
             </div>
         </section>
         <section class="shangjia">
             <header>商家信息</header>
            <div class="shangjia_tx">
                <p>{{shopDetail.name}}</p>
                <P>{{shopDetail.address}}</P>
                <p>营业时间:[{{shopDetail.opening_hours[0]}}]</p>
                <p @click="showLicenseImg(shopDetail.license.business_license_image)">
                    <span>营业执照</span>
                    <svg width="14" height="14" xmlns="http://www.w3.org/2000/svg" version="1.1" class="description_arrow" >
                        <path d="M0 0 L8 7 L0 14"  stroke="#bbb" stroke-width="1.5" fill="none"/>
                    </svg>
                </p>
                <p @click="showLicenseImg(shopDetail.license.catering_service_license_image)">
                    <span>餐饮服务许可证</span>
                    <svg width="14" height="14" xmlns="http://www.w3.org/2000/svg" version="1.1" class="description_arrow" >
                        <path d="M0 0 L8 7 L0 14"  stroke="#bbb" stroke-width="1.5" fill="none"/>
                    </svg>
                </p>
            </div>
         </section>
     </section>
     <!-- 遮掩img -->
     <div class="zheyan_img" v-if="showlicenseImg" @click="showlicenseImg=false"> 
         <img :src="imageBaseUrl+licenseImg"/>
     </div>
     <transition name="router-slid" mode="out-in">
         <router-view></router-view>
     </transition>
  </div>
</template>

<script>
import headerTop from  '../../../components/header/header'
import {mapState} from 'vuex'
import {getImgPath} from '../../../components/common/mixin'
    import {imageBaseUrl} from '../../../config/env'//图片服务器地址
export default {
    data()
    {
        return{
             licenseImg: null,
            showlicenseImg: false,
            imageBaseUrl
        }
    },mixins: [getImgPath],
    methods:{
         showLicenseImg(img){
                this.licenseImg = img;
                this.showlicenseImg = true;
        },
    } ,components: {
        headerTop
    },computed: {
        ...mapState([
            'shopDetail'
        ])
    },mounted () {
        console.log(JSON.stringify(this.shopDetail));
    }
}
</script>

<style lang="scss" scoped>
    @import '../../../style/mixin.scss';
    .rating_page{
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom: 0;
    padding-top: 1.95rem;
        background:#ededed;
        z-index: 18;
    }
    .shopDetail_container{
        margin-top: 0.3rem;
        background: #fff;
       @include wh(100%,8.8rem);
        header{
           padding:0.5rem;
            @include sc(0.75rem,#666);         
            border-bottom: 0.025rem solid #f1f1f1;;   
        }
        .actibities_ul{
           padding:0.6rem;
           li{
               padding-bottom: 0.2rem;
               span:nth-of-type(1)
               {
                   @include sc(0.5rem,#fff);
                   border:1px;
                   border-radius: 0.2rem;
                   padding:0.2rem;
               }
               span:nth-of-type(2)
               {
                   @include sc(0.55rem,#666);
               }
           }
        }
    }
.shiping_jianca{
    background: #fff;
    @include wh(100%,6rem);
    margin-top: 0.4rem;
    .indesc_container{
        border-bottom: 0.025rem solid #f1f1f1;
        display: flex;
        justify-content: space-between;
        padding:.2rem;
        align-items: center;
        line-height: 1.8rem;
        .indesc_title{
            @include sc(0.75rem,#666);
        }
        .indesc_text{
            span{
                @include sc(0.7rem,#999);
            }
            svg{
                @include wh(0.5rem,0.5rem)
            }
        }
    }
}
.img_rout{
    display: flex;
    padding:0.6rem;
    svg{
        @include wh(2rem,2rem);
    }
    .jianca_tx{
        p{
            padding-bottom: 0.5rem;
            padding-left: 0.5rem;
        }
        p:nth-of-type(1)
        {
            @include sc(0.5rem,#666);
            .Success{
              color: rgb(126, 211, 33);
              padding-left:0.3rem;
            }
            .Error{
                color:red;
                padding-left:0.3rem;
            }
        }
        p:nth-of-type(2)
        {
            @include sc(0.5rem,#666);
        }
    }
}
.shangjia{
    background: #fff;
    margin-top:0.6rem;
    header{
        padding:0.4rem;
        @include sc(0.8rem,#666);
        border-bottom:0.025rem solid #f1f1f1;
    }
}
.shangjia_tx{
    p{
        padding:0.5rem;
        border-bottom: 0.025rem solid #f1f1f1;
        @include sc(0.6rem,#666);
    }
    p:nth-of-type(4)
    {
        display: flex;
        justify-content:space-between;
    }
    P:nth-of-type(5)
    {
        display: flex;
        justify-content: space-between;
    }
}
.zheyan_img{
    position: fixed;
    bottom: 0;
    left:0;
    top:0;
    right: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 101;
    img{
        width: 100%;
        @include center;
    }
}
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-active {
        opacity: 0;
    }
    .router-slid-enter-active, .router-slid-leave-active {
        transition: all .4s;
    }
    .router-slid-enter, .router-slid-leave-active {
        transform: translate3d(2rem, 0, 0);
        opacity: 0;
    }
</style>



